.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

html,
body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  background-color: #ffffff;
  height: 100%;
}

h1,
h2,
h3 {
  margin: 0px;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

.active {
  text-decoration: underline;
}

.layout {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.layout-section {
  margin-top: 130px;
  flex-grow: 1;
}

.container {
  max-width: 1200px;
  margin: 0px auto;
  padding: 0px 19px;
  overflow: hidden;
}

.backdropSpinner {
  min-height: 100vh;
}

.wrapperSpinner {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.section-title {
  opacity: 0;
  transform: translateX(20%);
}

.section-title-animated {
  margin-bottom: 45px;
  opacity: 0;
  transform: translateX(20%);
  animation: gotoleftTitle 1s linear forwards;
}

@keyframes gotoleftTitle {
  0% {
    opacity: 0;
    transform: translateX(20%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.product-details-container {
  display: flex;
  flex-direction: row;
}

@media only screen and (max-width: 820px) {
  .product-details-container {
    flex-direction: column;
  }
  .product-left,
  .product-right {
    width: 100% !important;
  }
  .product-visualls {
